@font-face {
    font-family: "BMJUA";
    src: url("../Fonts/BMJUA_ttf.ttf");
}

@media (min-width: 320px) and (max-width: 420px) {
    .body{
        font-family: BMJUA;
    }
    .Hompage_Container{
        display: block;
        width: 100%;
        height: 570px;
        text-align: center;
        font-family: BMJUA;
    }
    
    .Homepage_img {
        width: 180px;
        height: 180px;
    }
    .Homepage_ImgBox{
        display: block;
        margin-top: 33px;
        margin-left: auto;
        margin-right: auto;
        width: 200px;
        height: 200px;
        margin-bottom: 43px;
    }

    .Homepage_Btn{
        display: block;
        width: 200px;
        height: 40px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 25px;
        background: #FFFFFF;
        border: 1px solid rgba(196, 196, 196, 0.1);
        box-sizing: border-box;
        box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        font-family: BMJUA;
    }
    .Homepage_Btn:hover{
        display: block;
        width: 200px;
        height: 40px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 25px;
        border: 1px solid rgba(196, 196, 196, 0.1);
        box-sizing: border-box;
        box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        background-color: #87CEEB;
        font-weight: bold;
    }
}